@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&display=swap);
a:hover {
    text-shadow: 1px 2px #0EB36D;
    cursor: pointer;
}

a {
    text-decoration: none;
    font-weight: bold;
    color: black;
}

body {
    background-color: tomato;
    font-family: sans-serif;
}

.container {
    margin-top: 3em;
}
.top {
    font-size: 17px;
    top: 0.5em;
    width: 100vw;
    position: fixed;
    display: flex;
    justify-content: center;
    font-family: 'Ubuntu', sans-serif;
}

.top__bar {
    list-style: none;
    display: flex;
}

.top__item {
    margin-right: 10vw;
}

.top__item--last {
    margin-right: 0;
}
.nome {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    display: flex;
}

.nome h1 {
    font-family: IBM Plex Mono, monospace;
    font-size: 110px;
  }

/*THIS IS FOR TYPEWRITER EFFECT (CURSOR)*/
span {
    border-right: .05em solid;
    -webkit-animation: caret 1s steps(1) infinite;
            animation: caret 1s steps(1) infinite;
}
  
@-webkit-keyframes caret {
    50% {
        border-color: transparent;
    }
}
  
@keyframes caret {
    50% {
        border-color: transparent;
    }
}
/*THIS IS FOR TYPEWRITER EFFECT (CURSOR)*/
.redes {
    position:absolute;
    bottom:0;
    right: auto;
    width:100%;
    margin-bottom: 1em;
    display: flex;
    justify-content: space-evenly;
}

.redes {
    background-color: #0EB36D;
}

.redes i {
    color: #2e2e2e;
}

.fa-facebook:hover {
    color: #3b5998;
}

.fa-twitter:hover {
    color: #00acee;
}

.fa-instagram:hover {
    color: #e95950;
}

.fa-linkedin:hover {
    color: #0e76a8;
}

.fa-github:hover {
    color: white;
}



